@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,900&display=swap);
@font-face {
    font-family: 'HKGrotesk-Light';
    src: url(/static/media/HKGrotesk-Light.646f8450.otf);
}

@font-face {
    font-family: 'HKGrotesk-Regular';
    src: url(/static/media/HKGrotesk-Regular.eb9ec08a.otf);
}

@font-face {
    font-family: 'HKGrotesk-Medium';
    src: url(/static/media/HKGrotesk-Medium.1fcfefce.otf);
}

html {
  width: 100%;
}

div {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 0;
  font-family: 'HKGrotesk-Regular', 'sans-serif';
}

.Label {
  font-family: 'HKGrotesk-Regular', 'sans-serif';
}

*:focus {
    outline: none;
}

.HomePage {
  width: 100%;
  height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  position: relative;
  background-image: url(/static/media/faded-sierra-mountains.ecc1d504.png);
  background-position: center;
  background-size: cover;
}

header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 20px 20px;
}

p {
  margin: 0;
  padding: 0;
}

.SSALogo {
  width: 24px;
  height: auto;
  margin-right: 16px;
}

.SSATitle {
  font-size: 1rem;
  color: #707FB4;
  flex: 1 1;
  padding-top: 2px;
  font-family: 'Montserrat';
  font-weight: 300;
}


.HomePageMainContent {
  width: 100%;
  flex: 1 1;
}

/* MOBILE LAYOUT */
@media (max-width: 1023px) {

  .SSALogo {
    width: 28px;
    height: auto;
    margin-right: 16px;
  }

  .SSATitle {
    font-size: 18px;
    color: #676f95;
    flex: 1 1;
  }

}

.PeoplePage {
  width: 100%;
  background-color: #1C1B1F;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 0 60px;
  align-items: center;
}

.PeoplePageTextContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
}

.PeoplePageTitle {
  color: #FFFFFF;
  font-family: 'Montserrat';
  font-size: 40px;
  font-weight: 900;
  margin: 0;
  margin-bottom: 40px;
  text-align: center;
}

.PeoplePageText {
  color: #FFFFFF;
  font-size: 20px;
  text-align: center;
}

.PeopleList {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 80%;
  padding: 0;
  max-width: 750px;
}

.PersonLink {
  text-decoration: none;
}

.Person {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px 20px;
}

.PersonImage {
  width: 150px;
  height: 150px;
}

.PersonName {
  color: #FFFFFF;
  font-size: 20px;
  text-align: center;
  margin-top: 10px;
  font-weight: 500;
}

.PersonTitle {
  font-weight: 700;
  font-size: 11px;
  color: #ABABAB;
  text-align: center;
  margin-top: 4px;
}

/* MOBILE LAYOUT */
@media (max-width: 1023px) {
  .PeoplePageTitle {
    font-size: 30px;
  }

  .PeoplePageText {
    color: #FFFFFF;
    font-size: 18px;
  }
}

.WorkPage {
  width: 100%;
  background-color: #1C1B1F;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  overflow: hidden;
}

.WorkPageTextContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 120px 10%;
}

.WorkPageTitle {
  color: #FFFFFF;
  font-family: 'Montserrat';
  font-size: 40px;
  font-weight: 900;
  margin-bottom: 40px;
  text-align: center;
}

.WorkPageText {
  color: #FFFFFF;
  font-size: 20px;
  text-align: center;
}

.WorkPageCarouselContainer {
  margin-bottom: 120px;
}

.WorkPageCarouselItem {
  padding: 0 120px;
}

.WorkPageCarouselImage {
  width: 128px;
  height: auto;
}

/* MOBILE LAYOUT */
@media (max-width: 1023px) {
  .WorkPageTitle {
    color: #FFFFFF;
    font-size: 30px;
    font-weight: 900;
    margin-bottom: 40px;
    text-align: left;
  }

  .WorkPageText {
    color: #FFFFFF;
    font-size: 18px;
  }

  .WorkPageCarouselItem {
    padding: 0 64px;
  }

  .WorkPageCarouselImage {
    width: 86px;
    height: auto;
  }
}

.LaunchPage {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #000000;
  background-image: url(data:image/png;base64,);
  position: relative;
  overflow: hidden;
}

.LaunchPageMainContent {
  width: 100%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.LaunchPageGradientImage {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.LaunchPageMainContentTitle {
  margin: 0;
  font-size: 24px;
  color: #FFFFFF;
  margin-top: 130px;
  text-shadow: 0 1px 1px #000000;
  text-align: center;
  z-index: 1;
}

.LaunchPageMainContentText {
  margin: 0;
  font-size: 80px;
  font-family: 'Montserrat', 'sans-serif';
  color: #FFFFFF;
  margin-top: 100px;
  text-align: center;
  font-weight: 900;
  z-index: 1;
}

.LaunchPageDecorationFooter {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
}

.LaunchPageMountainRange {
  position: absolute;
  bottom: -100px;
  left: 0;
  right: 0;
  width: 100%;
  height: auto;
  z-index: 0;
}

.ContentScroller {
  align-self: baseline;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  cursor: pointer;
  z-index: 1;
  margin-top: 80px;
  padding: 20px;
  -webkit-transition: background-color .18s ease-out;
  transition: background-color .18s ease-out;
  border-radius: 10%;
  background-color: #FFFFFF10;
}

.ContentScroller:hover {
  background-color: #FFFFFF24;
}

.ContentScrollerTitle {
  font-family: 'Montserrat', Helvetica, Arial, san-serif;
  font-weight: 500;
  font-size: 13px;
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
}

.ContentScrollerImage {
  width: 48px;
  height: auto;
}

/* MOBILE LAYOUT */
@media (max-width: 1023px) {
  .LaunchPageMountainRange {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: auto;
    z-index: 0;
  }

  .LaunchPageMainContentTitle {
    font-size: 22px;
  }

  .LaunchPageMainContentText {
    font-size: 64px;
    margin-top: 84px;
  }
}

.App {
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

.RocketBoosterImage {
  visibility: visible;
  position: absolute;
  bottom: 0px;
  left: 10%;
}

.RocketImage {
  visibility: visible;
  position: absolute;
  bottom: 120px;
  left: calc(10% - 15px);
}

.RocketImageLaunch {
  visibility: visible;
  position: absolute;
  bottom: 120px;
  left: calc(10% - 15px);
}

.animate-blast-off {
  display: block;
  position: fixed;
  -webkit-animation: blast-off 1.1s linear;
          animation: blast-off 1.1s linear;
}

@-webkit-keyframes blast-off {
  100% {
    bottom: 110vh;
  }
}

@keyframes blast-off {
  100% {
    bottom: 110vh;
  }
}

.hidden {
  visibility: hidden;
}

/* MOBILE LAYOUT */
@media (max-width: 1023px) {

  .RocketBoosterImage {
    visibility: hidden;
  }

  .RocketImage {
    visibility: hidden;
    position: absolute;
    bottom: 120px;
    left: calc(10% - 15px);
  }

  .RocketImageLaunch {
    visibility: hidden;
    position: absolute;
    bottom: 120px;
    left: calc(10% - 15px);
  }

}

